body,
html{
  margin: 0;
  padding: 0;
}

* {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.quiz-container{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to bottom, #cff7f1, #70e2da);
}

.quiz-container_question{
border-radius: 0.5rem;
height: 400px;
width: 60%;
margin: 0 auto;
text-align: center;
background-color: #3f51b5;
color: #fff;
display: flex;
flex-direction: column;
justify-content: space-between;
font-size: 1.4rem;
box-shadow: 0 10px 40px #999;
}
.quiz-container_options{
padding: 2rem;
border-top: 1px solid #ccc;

}
.quiz-container_ul{
padding: 0;
display: grid;
grid-template-columns: 1fr 1fr;
grid-template-rows: 1fr 1fr;
grid-gap: 1rem;
list-style-type: none;
}
.quiz-container_li{
padding: 1rem;
background-color: #fff;
color: #000;
cursor: pointer;
border-radius: 0.5rem;
transition: all 0.5s ease;
}
.quiz-container_li:hover{
  box-shadow: 0 1px 10px #fff;
}